import { AfterViewInit, Component, forwardRef, Inject, Input, HostListener, HostBinding } from '@angular/core';
import { NxRadioCircleButton } from '../radio-circle-button';
import { NxRadioCircleComponent } from '../radio-circle.component';

/**
 * @deprecated Use `nx-circle-toggle` instead
 * @deletion-target 8.0.0
 */
@Component({
  selector: 'nx-radio-circle-svg-button',
  templateUrl: './radio-circle-svg-button.html',
  styleUrls: ['../radio-circle-button.scss'],
  providers: [
    {
      provide: NxRadioCircleButton, useExisting: forwardRef(() => NxRadioCircleSvgButtonComponent)
    }
  ]
})
export class NxRadioCircleSvgButtonComponent extends NxRadioCircleButton implements AfterViewInit {

  /** Sets the SVG used for the not-checked state. */
  @Input('nxSvg')
  svg: string;

  /** Sets the SVG used for the checked state. */
  @Input('nxSvgChecked')
  svgChecked: string;
  private _hover: boolean = false;

  /** @docs-private */
  @HostListener('mouseenter') onMouseEnter() {
    this._hover = true;
  }

  /** @docs-private */
  @HostListener('mouseleave') onMouseLeave() {
    this._hover = false;
  }

  constructor(@Inject(forwardRef(() => NxRadioCircleComponent)) radioCircle: NxRadioCircleComponent) {
    super(radioCircle);
    console.warn('`nx-radio-circle-svg-button` is deprecated. Use `nx-circle-toggle` instead.');
  }

  ngAfterViewInit(): void {
    if (!this.svg) {
      console.warn(`nx-radio-circle-svg-button: required field 'nxSvg' is not set`);
    }

    if (!this.svgChecked) {
      console.warn(`nx-radio-circle-svg-button: required field 'nxSvgChecked' is not set`);
    }
  }

  /** @docs-private */
  get svgUrl(): string {
    return (this.checked || this._hover) ? this.svgChecked : this.svg;
  }
}
