import {NxRadioCircleComponent} from './radio-circle.component';
import {ElementRef, Input, OnChanges, ViewChild} from '@angular/core';

let nextId = 0;

export abstract class NxRadioCircleButton implements OnChanges {

  /** @docs-private */
  id: number = ++nextId;

  /** Sets the label displayed below the circle. */
  @Input('nxLabel')
  label: string;

  /** Sets an additional hint displayed below the label. */
  @Input('nxHint')
  hint: string;

  /** Sets the value used in the model. */
  @Input('nxValue')
  value: any;

  /** @docs-private */
  @ViewChild('labelElement')
  labelElement: ElementRef;

  constructor(
    /** @docs-private */
    protected radioCircle: NxRadioCircleComponent
    ) {
  }

  ngOnChanges(): void {
    if (!this.label) {
      console.warn(`nx-radio-circle-icon-button/nx-radio-circle-svg-button: required field 'nxLabel' is not set`);
    }
  }

  get checked(): boolean {
    return this.radioCircle.selectedButton === this || null;
  }

  /** Whether the item is preselected. */
  @Input('nxSelected')
  set checked(checked: boolean) {
    if (checked) {
      this.radioCircle.selectedButton = this;
    } else {
      if (this.checked) {
        this.radioCircle.selectedButton = null;
      }
    }
  }

  /** @docs-private */
  get circleButtonId(): string {
    return this.radioCircle.radioCircleId + '-' + this.id;
  }

  /** @docs-private */
  get radioCircleId(): string {
    return this.radioCircle.radioCircleId;
  }
}
