import {Component, forwardRef, Inject, Input} from '@angular/core';
import {NxRadioCircleButton} from '../radio-circle-button';
import {NxRadioCircleComponent} from '../radio-circle.component';

/**
 * @deprecated Use `nx-circle-toggle` instead
 * @deletion-target 8.0.0
 */
@Component({
  selector: 'nx-radio-circle-icon-button',
  templateUrl: './radio-circle-icon-button.html',
  styleUrls: ['../radio-circle-button.scss'],
  providers: [
    {
      provide: NxRadioCircleButton, useExisting: forwardRef(() => NxRadioCircleIconButtonComponent)
    }
  ],
  host: {
    '[class.is-disabled]': 'radioCircle.disabled',
    '[attr.aria-disabled]': 'radioCircle.disabled'
  }
})
export class NxRadioCircleIconButtonComponent extends NxRadioCircleButton {

  /** Sets the id of the icon to display. */
  @Input('nxIcon')
  icon: string;

  constructor(
      /** @docs-private */
      @Inject(forwardRef(() => NxRadioCircleComponent)) public radioCircle: NxRadioCircleComponent
    ) {
    super(radioCircle);
    console.warn('`nx-radio-circle-icon-button` is deprecated. Use `nx-circle-toggle` instead.');
  }
}
