import { Component, ElementRef, EventEmitter, forwardRef, Input, Output, Renderer2, ViewChild } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

let nextId = 0;

/**
  * @deprecated Use `nx-circle-toggle` instead
  * @deletion-target 8.0.0
  */
@Component({
  selector: 'nx-checkbox-circle',
  templateUrl: 'checkbox-circle.component.html',
  styleUrls: ['checkbox-circle.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => NxCheckboxCircleComponent),
      multi: true
    }
  ],
  host: {
    '[class.is-checked]': 'checked'
  }
})
export class NxCheckboxCircleComponent implements ControlValueAccessor {

  private _id: string = (nextId++).toString();

  /**
   * Id of the checkbox.
   * If not set, the checkbox gets an incremented value by default.
   */
  @Input()
  public set id(value: string) {
    if (value !== this._id) {
      this._id = value;
    }
  }
  public get id() {
    return `nx-checkbox-circle-${this._id}`;
  }

  /** The name of the Checkbox Circle. */
  @Input()
  name: string;

  /** An event emitted when the checked value has changed. */
  @Output()
  checkedChange = new EventEmitter<boolean>(false);

  /** Whether the checkbox circle is checked. */
  @Input()
  checked: boolean;

  /** The name of the icon that is displayed. If not added, no icon will be displayed. */
  @Input('nxIcon')
  iconName: string;

  /** If set, the passed SVG will be displayed in the not-checked state. */
  @Input('nxSvg')
  svg: string;

  /** If set, the passed SVG will be displayed in the checked state. */
  @Input('nxSvgChecked')
  svgChecked: string;

  /** Label to be displayed under circle. */
  @Input('nxLabel')
  label: string;

  private onChangeCallback = (checked: boolean) => { };

  private onTouchedCallback = () => { };

  constructor() {
    console.warn('`nx-checkbox-circle` is deprecated. Use `nx-circle-toggle` instead.');
  }

  writeValue(value: boolean): void {
    this.checked = value;
    this.checkedChange.emit(value);
  }

  registerOnChange(onChange: any): void {
    this.onChangeCallback = onChange;
  }

  registerOnTouched(onTouched: any): void {
    this.onTouchedCallback = onTouched;
  }

  setDisabledState?(isDisabled: boolean): void {
  }

  /** @docs-private */
  toggle() {
    this.writeValue(!this.checked);
    this.onChangeCallback(this.checked);
  }

  /** @docs-private */
  get svgButton(): boolean {
    return !!this.svg && !!this.svgChecked;
  }

  /** @docs-private */
  get svgUrl(): string {
    return this.checked ? this.svgChecked : this.svg;
  }
}
